import React, { useRef, useState } from 'react'
import upload_file from 'src/assets/images/upload_files.png'
import delete_file from 'src/assets/images/delete_icons.jpg'
import 'src/styles.css'
import {
  CButton,
  CCard,
  CCardBody,
  CCol,
  CCollapse,
  CDatePicker,
  CForm,
  CFormInput,
  CFormLabel,
  CImage,
  CModal,
  CModalBody,
  CModalFooter,
  CModalHeader,
  CModalTitle,
  CRow,
  CToast,
  CToastBody,
  CToaster,
  CToastHeader,
} from '@coreui/react-pro'
import {
  serviceDeleteEpoc,
  serviceExistEpoc,
  serviceSearchEpoc,
  serviceUploadFile,
  exportToCSVEpoc,
} from 'src/service/epoc_file/epoc'
import moment from 'moment'
import CustomTable from 'src/components/custom_table/CustomTable'
import CustomToast from 'src/components/custom_toast/CustomToast'
import CustomSearchInput from 'src/components/custom_search_input/CustomSearchInput'
import { useDispatch } from 'react-redux'
import { progressAction } from 'src/store/actions/progressActions'
import { columns } from './epoc_table_utils'
import { exportToExcel } from 'src/reusable/ExcelExport'
import { CSVLink } from 'react-csv'

const EPOCFile = () => {
  const dispatch = useDispatch()
  const [details, setDetails] = useState([])
  const [showModal, setShowModal] = useState(false)
  const [showModalExist, setShowModalExist] = useState(false)
  const [showModalDelete, setShowModalDelete] = useState(false)
  const [file, setFile] = useState([])
  const [dateFile, setDateFile] = useState(moment().format('MM/DD/YYYY'))
  const [toast, addToast] = useState(0)
  const [record, setRecord] = useState([])
  const [header, setHeader] = useState([])
  const [trailer, setTrailer] = useState([])
  const [cantPag, setCantPag] = useState(0)
  const [itemPerPage, setitemPerPage] = useState(10)
  const toaster = useRef()
  const [canSearch, setCanSearch] = useState(false)
  const [resetFilter, setResetFilter] = useState(false)
  const [totalElement, setTotalElement] = useState(0)
  const [searchData, setSearchData] = useState({})
  const [datos, setDatos] = useState({})
  const [csvData, setCSVdata] = useState([])

  const scopedColumns = {
    show_details: (item) => {
      return (
        <td className="py-2">
          <CButton
            color="primary"
            variant="outline"
            shape="square"
            size="sm"
            onClick={() => {
              toggleDetails(item)
            }}
          >
            {details.includes(item) ? 'Hide' : 'Show'}
          </CButton>
        </td>
      )
    },
    details: (item) => {
      let index = {}
      var first_column = []
      var second_column = []
      let variables_name = []
      let variables_values = []

      if (datos) {
        let details_rec = Object.keys(datos.records[item._id])
        for (let i = 0; i < details_rec.length - 1; i++) {
          let variables = Object.keys(datos.records[item._id][details_rec[i]])
          variables_name.push(variables)
          let values = Object.values(datos.records[item._id][details_rec[i]])
          variables_values.push(values)
        }

        let details_rec_optinal = Object.keys(datos.records[item._id].optional_records)
        for (let i = 0; i < details_rec_optinal.length; i++) {
          var variables = Object.keys(
            datos.records[item._id].optional_records[details_rec_optinal[i]],
          )
          variables_name.push(variables)
          var values = Object.values(
            datos.records[item._id].optional_records[details_rec_optinal[i]],
          )
          variables_values.push(values)
        }

        var count = 0
        for (let j = 0; j < variables_name.length; j++) {
          for (let i = 0; i < variables_name[j].length; i++) {
            if (
              variables_name[j][i] !== 'record_identifier' &&
              variables_name[j][i] !== 'record_number' &&
              variables_name[j][i] !== 'reserved' &&
              // variables_name[j][i] !== 'terminal_sequence_number' &&
              // variables_name[j][i] !== 'point_of_service_data_code' &&
              variables_name[j][i] !== 'message_type' &&
              variables_name[j][i] !== 'primary_account_number' &&
              variables_name[j][i] !== 'amount_reconciliation' &&
              variables_name[j][i] !== 'card_acceptor_name' &&
              variables_name[j][i] !== 'dc_flag'
            ) {
              if (count < 35) {
                index['variable'] = variables_name[j][i]
                index['valor'] = variables_values[j][i]
                first_column.push(index)
                index = new Object()

                count = count + 1
              } else {
                index['variable'] = variables_name[j][i]
                index['valor'] = variables_values[j][i]
                second_column.push(index)
                index = new Object()
              }
            }
          }
        }
        //////////////////////////////

        for (let i = 0; i < first_column.length; i++) {
          const variable_to_format = first_column[i].variable
          const variable_new_format =
            variable_to_format.charAt(0).toLocaleUpperCase() + variable_to_format.slice(1)
          first_column[i].variable = variable_new_format.replace(/_/g, ' ')
        }
        for (let i = 0; i < second_column.length; i++) {
          const variable_to_format = second_column[i].variable
          const variable_new_format =
            variable_to_format.charAt(0).toLocaleUpperCase() + variable_to_format.slice(1)
          second_column[i].variable = variable_new_format.replace(/_/g, ' ')
        }

        /////////////////////////////////
        return (
          <CCollapse visible={details.includes(item)}>
            <CCardBody>
              <CRow>
                <CCol xs={6}>
                  {first_column.map((dat) => (
                    <div key={dat.index}>
                      {dat.variable} : {dat.valor}
                    </div>
                  ))}
                </CCol>
                <CCol xs={6}>
                  {second_column.map((dat) => (
                    <div key={dat.index}>
                      {dat.variable} : {dat.valor}
                    </div>
                  ))}
                </CCol>
              </CRow>
            </CCardBody>
          </CCollapse>
        )
      } else {
        setRecord([])
        setHeader([])
        setTrailer([])
      }
    },
  }
  const toggleDetails = (item) => {
    if (details.includes(item)) {
      setDetails([])
    } else {
      setDetails([item])
    }
  }
  const uploadFile = async () => {
    var formData = new FormData()
    formData.append('file', file[0])
    setShowModal(false)
    let start = new Date().getTime()
    dispatch(
      progressAction({
        percentage: 0,
        isUpload: true,
        startTime: start,
        durationTime: 0,
        speed: 0,
      }),
    )
    const response = await serviceUploadFile(formData, dispatch, progressAction, start)
    if (response.status === '200') {
      setFile([])
      addToast(CustomToast({ msg: 'File uploaded successfully' }))
      setDateFile((state) => {
        state = moment().format('MM/DD/YYYY')
        return state
      })
      const data = {
        page: 0,
        size: itemPerPage,
        filter: {},
        sort: { sequence_number: '0' },
      }
      setCanSearch(true)
      loadData(data)
    }
  }
  const search = () => {
    const data = {
      page: 0,
      size: itemPerPage,
      filter: {},
      sort: { sequence_number: '0' },
    }
    setResetFilter(true)
    setCanSearch(true)
    loadData(data)
  }

  const changePageItemPerPage = (data) => {
    setitemPerPage(data.size)
    if (canSearch) {
      loadData(data)
    }
  }
  const loadData = async (data, date = null) => {
    let dataSearch = {
      upload_file_date: date !== null ? date : moment(dateFile).format('YYYY-MM-DD'),
      ...data,
    }
    setSearchData(dataSearch)
    const response = await serviceSearchEpoc(dataSearch)
    if (response.status === '200') {
      setDatos(response.data)
      var record_new = []
      let index = {}
      var variables_of_interesest = [
        'message_type',
        'primary_account_number',
        'amount_reconciliation',
        'card_acceptor_name',
        'dc_flag',
      ]
      var cant_records = Object.keys(response.data.records)
      for (let h = 0; h < cant_records.length; h++) {
        for (let j = 0; j < variables_of_interesest.length; j++) {
          let detail_record = Object.keys(response.data.records[h])
          for (let i = 0; i < detail_record.length - 1; i++) {
            let clave = detail_record[i]
            if (response.data.records[h][clave][variables_of_interesest[j]]) {
              index[variables_of_interesest[j]] =
                response.data.records[h][clave][variables_of_interesest[j]]
            }
          }
          var optional_records = Object.keys(response.data.records[0].optional_records)
          for (let i = 0; i < optional_records.length; i++) {
            let clave = optional_records[i]
            if (response.data.records[h].optional_records[clave][[variables_of_interesest[j]]]) {
              index[variables_of_interesest[j]] =
                response.data.records[h].optional_records[clave][[variables_of_interesest[j]]]
            }
          }
        }
        record_new.push(index)
        index = new Object()
      }
      if (response.data) {
        const { header, trailer, total_elements, total_pages } = response.data
        var new_trailer = []
        var variables_trailer_interest = [
          'trailer_flag_identifier',
          'reversal_credit_amount_atm',
          'gross_credit_amount_pos',
          'reversal_fee_pos',
          'reserved',
        ]
        var trailer_record = Object.keys(trailer)
        for (let j = 0; j < variables_trailer_interest.length; j++) {
          for (let i = 0; i < trailer_record.length; i++) {
            if (trailer[trailer_record[i]][variables_trailer_interest[j]]) {
              new_trailer[variables_trailer_interest[j]] =
                trailer[trailer_record[i]][variables_trailer_interest[j]]
            }
          }
        }
        setRecord(record_new)
        setHeader(header)
        setTrailer(new_trailer)
        setCantPag(total_pages)
        setTotalElement(total_elements)
      } else {
        setRecord([])
        setHeader([])
        setTrailer([])
        setCantPag(0)
        setTotalElement(0)
      }
      setResetFilter(false)
    }
    ///////////////////////
    let dataCSV = []
    var date_export = { date: moment(dateFile).format('YYYY-MM-DD') }
    const response_data = await exportToCSVEpoc(date_export)

    let arrFilas = response_data.split('\r\n')
    for (let i = 0; i < arrFilas.length; i++) {
      var datos_separados = arrFilas[i].split(',')
      dataCSV.push(datos_separados)
    }
    setCSVdata(dataCSV)
    //////////////////////
  }
  const exitsFile = async (e) => {
    e.preventDefault()
    // let contentBuffer = await readFile(file[0])
    // let header = contentBuffer.split('\n')[0]
    setShowModal(false)
    const response = await serviceExistEpoc()
    if (response.data) {
      setShowModalExist(true)
    } else if (response.data !== null) {
      uploadFile()
    }
  }
  const readFile = async (file) => {
    return new Promise((resolve, reject) => {
      let reader = new FileReader()
      reader.onload = () => {
        resolve(reader.result)
      }
      reader.onerror = reject
      reader.readAsText(file)
    })
  }
  const overwrite = (e) => {
    e.preventDefault()
    setShowModalExist(false)
    uploadFile()
  }
  const deleteFile = async (e) => {
    e.preventDefault()
    setShowModalDelete(false)
    let date = moment(dateFile).format('YYYY-MM-DD')
    const response = await serviceDeleteEpoc({ date: `${date}` })
    if (response.status === '200') {
      setRecord([])
      setHeader([])
      setTrailer([])
      setCantPag(0)
    }
  }
  // const exportData = async () => {
  //   let data = { ...searchData, size: totalElement }
  //   const response = await serviceSearchEpoc(data)
  //   if (response.status === '200') {
  //     if (response.data) {
  //       const { records } = response.data
  //       var key_detail_record = Object.keys(records[0])
  //       var variables_of_interesest = []
  //       for (let i = 0; i < key_detail_record.length - 1; i++) {
  //         var ind = Object.keys(records[0][key_detail_record[i]])
  //         variables_of_interesest = variables_of_interesest.concat(ind)
  //       }

  //       var record_new = []
  //       let index = {}
  //       var cant_records = Object.keys(response.data.records)
  //       for (let h = 0; h < cant_records.length; h++) {
  //         for (let j = 0; j < variables_of_interesest.length; j++) {
  //           let detail_record = Object.keys(response.data.records[h])
  //           for (let i = 0; i < detail_record.length - 1; i++) {
  //             let clave = detail_record[i]
  //             if (response.data.records[h][clave][variables_of_interesest[j]]) {
  //               index[variables_of_interesest[j]] =
  //                 response.data.records[h][clave][variables_of_interesest[j]]
  //             }
  //           }
  //           var optional_records = Object.keys(response.data.records[0].optional_records)
  //           for (let i = 0; i < optional_records.length; i++) {
  //             let clave = optional_records[i]
  //             if (response.data.records[h].optional_records[clave][[variables_of_interesest[j]]]) {
  //               index[variables_of_interesest[j]] =
  //                 response.data.records[h].optional_records[clave][[variables_of_interesest[j]]]
  //             }
  //           }
  //         }
  //         record_new.push(index)
  //         index = new Object()
  //       }
  //       exportToExcel(record_new, 'EPOC')
  //     }
  //   }
  // }
  return (
    <>
      <CRow>
        <CCol xs={12}>
          <CCard className="mb-4">
            <CCardBody>
              <CRow>
                <CCol xs={12} className="mb-4">
                  <CRow>
                    <CCol xs={2}>
                      <CButton
                        component="input"
                        type="button"
                        color="primary"
                        value="Upload File"
                        onClick={() => {
                          setShowModal(true)
                        }}
                      />
                    </CCol>
                    <CCol xs={4}>
                      <CDatePicker
                        locale="en-US"
                        inputReadOnly
                        date={dateFile}
                        dayFormat={'2-digit'}
                        onDateChange={(e) => {
                          setDateFile(moment(e).format('YYYY-MM-DD'))
                        }}
                      />
                    </CCol>
                    <CCol xs={1}>
                      <CButton
                        component="input"
                        type="button"
                        color="info"
                        value="Search"
                        onClick={() => {
                          search()
                        }}
                      />
                    </CCol>
                    {record.length > 0 ? (
                      <>
                        <CCol xs={1}>
                          <CButton
                            component="input"
                            type="button"
                            color="danger"
                            value="Delete"
                            onClick={() => {
                              setShowModalDelete(true)
                            }}
                          />
                        </CCol>
                        <CCol xs={1}>
                          {/* <CButton
                            component="input"
                            type="button"
                            color="primary"
                            value="Export"
                            onClick={() => {
                              exportData()
                            }}
                          /> */}
                          <CSVLink data={csvData} filename="EPOC" className="btn btn-primary">
                            Export
                          </CSVLink>
                        </CCol>
                      </>
                    ) : (
                      ''
                    )}
                  </CRow>
                </CCol>
                <CCol xs={6}>
                  <h3>Header Info</h3>
                  <CRow>
                    <CCol xs={6}>
                      <p className="text-dark">Logo: {header.logo}</p>
                      <p className="text-dark">Creation date: {header.creation_date}</p>
                      <p className="text-dark">Bussiness date: {header.bussiness_date}</p>
                    </CCol>
                    <CCol xs={6}>
                      <p className="text-dark">Currency: {header.currency}</p>
                      <p className="text-dark">Reserved: {header.reserved}</p>
                    </CCol>
                  </CRow>
                </CCol>
                <CCol xs={6}>
                  <h3>Trailer Info</h3>
                  <CRow>
                    <CCol xs={6}>
                      <p className="text-dark">Reversal fee pos: {trailer.reversal_fee_pos}</p>
                      <p className="text-dark">
                        Gross credit amount pos: {trailer.gross_credit_amount_pos}
                      </p>
                      <p className="text-dark">
                        Reversal credit amount atm: {trailer.reversal_credit_amount_atm}
                      </p>
                    </CCol>
                    <CCol xs={6}>
                      <p className="text-dark">
                        Trailer flag identifier: {trailer.trailer_flag_identifier}
                      </p>
                      <p className="text-dark">Reserved: {trailer.reserved}</p>
                    </CCol>
                  </CRow>
                </CCol>
                <CCol xs={12}>
                  <CustomTable
                    record={record}
                    columns={columns}
                    scopedColumns={scopedColumns}
                    cantPag={cantPag}
                    searchData={changePageItemPerPage}
                    resertFilter={resetFilter}
                  />
                </CCol>
              </CRow>
            </CCardBody>
          </CCard>
        </CCol>
      </CRow>
      <CModal className="modal_windows" visible={showModal}>
        <CForm className="windows" onSubmit={exitsFile}>
          <img
            style={{ width: '35px', marginLeft: '35px', marginTop: '30px' }}
            src={upload_file}
            alt=""
          />
          <CButton
            className="close_windows"
            style={{
              width: '10px',
              marginLeft: '370px',
              marginTop: '-20px',
              border: 'none',
            }}
            variant="ghost"
            onClick={() => setShowModal(false)}
          ></CButton>
          <CModalBody>
            <div style={{ marginTop: '10px' }}>
              <CFormLabel style={{ fontSize: '20px' }} htmlFor="formFile">
                Select the file you want to upload
              </CFormLabel>
              <CFormInput
                style={{ marginTop: '10px' }}
                type="file"
                placeholder="select a file"
                id="formFile"
                onChange={(e) => {
                  setFile(e.target.files)
                }}
              />
            </div>
            <div style={{ marginTop: '50px' }}>
              <CButton
                style={{ width: '150px', height: '45px', marginLeft: '63px' }}
                color="dark"
                variant="outline"
                onClick={() => setShowModal(false)}
              >
                Close
              </CButton>
              <CButton
                style={{ width: '150px', height: '45px', marginLeft: '40px' }}
                color="primary"
                type="submit"
                className="button"
              >
                Upload
              </CButton>
            </div>
          </CModalBody>
        </CForm>
      </CModal>

      <CModal className="modal_windows" visible={showModalExist}>
        <CForm className="windows" onSubmit={overwrite}>
          <img
            style={{ width: '35px', marginLeft: '43px', marginTop: '30px', position: 'absolute' }}
            src={delete_file}
            alt=""
          />
          <div className="icono_delete_1"></div>
          <div className="icono_delete_2"></div>
          <CButton
            className="close_windows"
            style={{
              marginLeft: '450px',
              marginTop: '-155px',
              border: 'none',
              position: 'relative',
            }}
            variant="ghost"
            onClick={() => setShowModalExist(false)}
          ></CButton>
          <div>
            <CFormLabel
              style={{ fontSize: '25px', marginTop: '2px', marginLeft: '40px', fontWeight: '550' }}
              htmlFor="formFile"
            >
              Delete record
            </CFormLabel>
            <br></br>
            <CFormLabel
              style={{ fontSize: '17px', marginLeft: '40px', marginRight: '33px' }}
              htmlFor="formFile"
            >
              There’s already a file for this day. Do you want to overwrite it?
            </CFormLabel>
          </div>

          <div style={{ marginTop: '30px', marginBottom: '25px', marginLeft: '10px' }}>
            <CButton
              style={{
                width: '150px',
                height: '45px',
                marginLeft: '70px',
                fontSize: '17px',
                color: 'black',
              }}
              color="dark"
              variant="outline"
              onClick={() => setShowModalExist(false)}
            >
              Close
            </CButton>
            <CButton
              className="delete_button"
              style={{
                width: '150px',
                height: '45px',
                marginLeft: '40px',
                fontSize: '17px',
                fontWeight: '550',
              }}
              type="submit"
            >
              Overwrite
            </CButton>
          </div>
        </CForm>
      </CModal>

      <CModal className="modal_windows" visible={showModalDelete}>
        <CForm className="windows" onSubmit={deleteFile}>
          <img
            style={{ width: '35px', marginLeft: '43px', marginTop: '30px', position: 'absolute' }}
            src={delete_file}
            alt=""
          />
          <div className="icono_delete_1"></div>
          <div className="icono_delete_2"></div>
          <CButton
            className="close_windows"
            style={{
              marginLeft: '450px',
              marginTop: '-155px',
              border: 'none',
              position: 'relative',
            }}
            variant="ghost"
            onClick={() => setShowModalDelete(false)}
          ></CButton>
          <div>
            <CFormLabel
              style={{ fontSize: '25px', marginTop: '2px', marginLeft: '40px', fontWeight: '550' }}
              htmlFor="formFile"
            >
              Delete record
            </CFormLabel>
            <br></br>
            <CFormLabel
              style={{ fontSize: '17px', marginLeft: '40px', marginRight: '33px' }}
              htmlFor="formFile"
            >
              Do you want to delete this file?
            </CFormLabel>
          </div>

          <div style={{ marginTop: '30px', marginBottom: '25px', marginLeft: '10px' }}>
            <CButton
              style={{
                width: '150px',
                height: '45px',
                marginLeft: '70px',
                fontSize: '17px',
                color: 'black',
              }}
              color="dark"
              variant="outline"
              onClick={() => setShowModalDelete(false)}
            >
              Close
            </CButton>
            <CButton
              className="delete_button"
              style={{
                width: '150px',
                height: '45px',
                marginLeft: '40px',
                fontSize: '17px',
                fontWeight: '550',
              }}
              type="submit"
            >
              Delete
            </CButton>
          </div>
        </CForm>
      </CModal>
      <CToaster ref={toaster} push={toast} placement="top-end" />
    </>
  )
}

export default EPOCFile
